/*
 * Timebar Diary - Task Screen Styles
 * : src/main/frontend/src/styles/screens/task.css
 */

/*     */

.task-screen {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/*  /  */

.task-filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-filter-group {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.task-chip {
  padding: 6px 12px; /*    */
  font-size: var(--font-small);
  border-radius: var(--radius-md);
  background: var(--color-surface-soft);
  color: var(--color-text-muted);
  cursor: pointer;
  transition: all 0.15s ease;
}

.task-chip--active {
  background: var(--color-primary);
  color: var(--color-surface);
}

/*  +    */

.task-main {
  /* : task-main     (1.3:1) */
  display: grid;
  grid-template-columns: minmax(0, 1.3fr) minmax(0, 1fr);
  gap: 24px;
}

@media (max-width: 960px) {
  .task-main {
    grid-template-columns: minmax(0, 1fr);
  }
}

/*   */

.task-list-card {
  overflow: hidden;
  padding: 0; /*      */
}

.task-list {
  width: 100%;
  border-collapse: collapse;
  font-size: var(--font-body);
}

.task-list th {
  text-align: left;
  padding: 10px 12px; /*     */
  font-size: var(--font-small);
  color: var(--color-text-muted);
  border-bottom: 1px solid var(--color-border);
}

.task-list td {
  padding: 10px 12px; /*     */
  border-bottom: 1px solid var(--color-surface-soft);
  cursor: pointer;
  font-size: var(--font-body);
}

.task-row:hover {
  background: var(--color-surface-soft); /* Hover   */
}

.task-row__status {
  font-size: var(--font-xs);
  padding: 3px 8px; /*     */
  border-radius: var(--radius-sm);
  font-weight: 500;
  text-transform: uppercase;
}

.task-row__status--done {
  background: var(--color-success);
  color: var(--color-surface);
}

.task-row__status--pending {
  background: var(--color-warning);
  color: var(--color-surface);
}

.task-row__status--overdue {
  background: var(--color-danger);
  color: var(--color-surface);
}

/* Task Detail / Edit (TASK-002) */
.task-detail-card {
  padding: 20px;
}

.task-detail-title {
  font-size: var(--font-h3);
  margin-bottom: 10px;
}

.task-detail-meta {
  font-size: var(--font-small);
  color: var(--color-muted);
  margin-bottom: 20px;
  display: flex;
  gap: 15px;
}

.task-detail-notes {
  border-radius: var(--radius-lg);
  min-height: 100px; /*    */
  padding: 12px;
  background: var(--color-surface-soft);
  font-size: var(--font-body);
  border: 1px solid var(--color-border);
}

.task-edit-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}