/*
 * Timebar Diary - Pastel Theme Overrides
 * : src/main/frontend/src/styles/theme/pastel.css
 */

:root[data-theme="pastel"] {
  --color-bg: #fffcfd; /*    */
  --color-surface: #ffffff;
  --color-surface-soft: #fdf2ff;
  --color-border: #f9e2fb; /*    */

  --color-text: #4b5563;
  --color-muted: #a1a1aa;

  --color-primary: #f472b6; /*   Primary */
  --color-primary-soft: rgba(244, 114, 182, 0.15); /* Soft    */
  
  --color-success: #84cc16; /*  */
  --color-warning: #fde047; /*   */
  --color-info: #7dd3fc; /*  */

  /*   */
  --color-bg-elevated: var(--color-surface);
  --color-accent: var(--color-primary);
  --color-text-muted: var(--color-muted);
  --color-danger: #fb7185;

  /* Shadow   */
  --shadow-card: 0 4px 12px rgba(244, 114, 182, 0.1); 

  /*   */
  .app-shell__sidebar {
    background: var(--color-surface-soft);
  }

  .btn--primary,
  .primary-button {
    box-shadow: 0 4px 12px var(--color-primary-soft);
  }

  .tabbar__item--active {
    border: 1px solid var(--color-border);
  }
}