/*    */
.admin-page {
  padding: 24px;
  max-width: 1400px;
  margin: 0 auto;
  background-color: #f8fafc; /* :   */
  min-height: 100vh;
}

.admin-page__head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  flex-wrap: wrap;
  gap: 16px;
}

/* 2  ( + ) */
.admin-two-col {
  display: grid;
  grid-template-columns: 320px 1fr;
  gap: 16px;
  align-items: start;
}

/*   */
.admin-ui__tabs {
  display: flex;
  gap: 4px;
  border-bottom: 1px solid #f0f0f0;
  margin-bottom: 16px;
  overflow-x: auto;
  padding-bottom: 8px;
}

.admin-ui__tab {
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  color: #64748b;
  cursor: pointer;
  border: none;
  background: transparent;
  transition: all 0.2s;
  white-space: nowrap;
}

.admin-ui__tab:hover {
  background-color: #f1f5f9;
  color: #0f172a;
}

.admin-ui__tab[data-state="active"] {
  background-color: #eef2ff;
  color: #6366f1;
  font-weight: 700;
}

/*    */
.admin-card-fullHeight {
  height: 100%;
}

/*     */
.admin-ui__progressRow {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}
.admin-ui__progressLabel {
  width: 60px;
  font-size: 13px;
  color: #64748b;
  text-align: right;
}

/*   () */
.admin-ui__miniCard .ant-card-body {
  padding: 12px;
}

/*   */
@media (max-width: 1024px) {
  .admin-two-col {
    grid-template-columns: 1fr; /* / 1 */
  }
}

/* ---    --- */
.admin-page {
  padding: 24px;
  max-width: 1600px; /* CS   */
  margin: 0 auto;
  background-color: #f8fafc;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.admin-page__head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 16px;
  flex-shrink: 0;
}

/*  2  ( ) */
.admin-two-col {
  display: grid;
  grid-template-columns: 320px 1fr;
  gap: 16px;
  align-items: start;
}

/* --- [NEW] CS   (/ ) --- */
.admin-cs-wrapper {
  display: grid;
  grid-template-columns: 380px 1fr; /*    */
  gap: 16px;
  height: calc(100vh - 140px); /*    */
  min-height: 600px;
}

/*    */
.admin-cs-list-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: #fff;
  border: 1px solid #f0f0f0;
  border-radius: 8px;
}

.admin-cs-list-header {
  padding: 16px;
  border-bottom: 1px solid #f0f0f0;
  background: #fff;
  flex-shrink: 0;
}

.admin-cs-list-body {
  flex: 1;
  overflow-y: auto;
  background: #fafafa;
}

/*    */
.cs-ticket-item {
  padding: 16px;
  border-bottom: 1px solid #f0f0f0;
  cursor: pointer;
  transition: all 0.2s;
  background: #fff;
}
.cs-ticket-item:hover { background-color: #f8fafc; }
.cs-ticket-item.active {
  background-color: #eef2ff;
  border-left: 4px solid #6366f1;
}

.cs-ticket-top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
}
.cs-ticket-title { font-weight: 600; color: #1e293b; margin-bottom: 4px; }
.cs-ticket-desc { 
  font-size: 13px; color: #64748b; 
  white-space: nowrap; overflow: hidden; text-overflow: ellipsis; 
}

/*    */
.admin-cs-detail-card {
  height: 100%;
  overflow-y: auto;
  background: #fff;
  border: 1px solid #f0f0f0;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}

/*   */
@media (max-width: 900px) {
  .admin-cs-wrapper {
    grid-template-columns: 1fr; /*  1 */
    height: auto;
  }
  .admin-cs-list-card { height: 500px; }
  .admin-cs-detail-card { height: auto; min-height: 500px; }
}