/* FILE: src/screens/address/AddressBookScreen.css */
/* Layout */
.address-book-page {
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  
  .ab-layout {
    display: flex;
    flex: 1;
    gap: 24px;
    height: 100%;
    overflow: hidden;
    position: relative;
  }
  
  /* Sidebar */
  .ab-sidebar {
    width: 240px;
    display: flex;
    flex-direction: column;
    border-right: 1px solid var(--tf-border);
    padding-right: 20px;
  }
  
  .ab-search-box {
    position: relative;
    margin-bottom: 20px;
  }
  .ab-search-icon { position: absolute; left: 10px; top: 50%; transform: translateY(-50%); color: #94a3b8; }
  .ab-search-input {
    width: 100%;
    padding: 10px 10px 10px 36px;
    border: 1px solid var(--tf-border);
    border-radius: 12px;
    font-size: 14px;
    outline: none;
    background: white;
  }
  .ab-search-input:focus { border-color: var(--tf-primary); }
  
  .ab-group-list { display: flex; flex-direction: column; gap: 4px; overflow-y: auto; }
  .ab-group-label-row {
    display: flex; justify-content: space-between; align-items: center;
    margin-bottom: 8px; margin-top: 16px; padding-left: 8px; padding-right: 4px;
  }
  .ab-group-label { font-size: 11px; font-weight: 700; color: #94a3b8; }
  .ab-group-setting-btn { background: none; border: none; cursor: pointer; color: #94a3b8; }
  .ab-group-setting-btn:hover { color: var(--tf-primary); }
  
  .ab-group-divider { height: 1px; background: var(--tf-border); margin: 8px 0; }
  
  .ab-group-item {
    display: flex; align-items: center; gap: 10px;
    padding: 10px 12px;
    border-radius: 8px;
    background: transparent; border: none;
    cursor: pointer;
    font-size: 14px; color: var(--tf-text);
    transition: all 0.2s;
  }
  .ab-group-item:hover { background: #f1f5f9; }
  .ab-group-item.active { background: var(--tf-primary-soft); color: var(--tf-primary); font-weight: 600; }
  .ab-dot { width: 6px; height: 6px; background: #cbd5e1; border-radius: 50%; }
  .ab-group-item.active .ab-dot { background: var(--tf-primary); }
  .ab-count { margin-left: auto; font-size: 11px; color: #94a3b8; background: #fff; padding: 2px 6px; border-radius: 10px; }
  
  /* Main Content */
  .ab-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding-bottom: 40px;
  }
  
  .ab-list-header {
    display: flex; justify-content: space-between; align-items: center;
    margin-bottom: 16px;
    font-size: 13px; color: #64748b;
  }
  
  .ab-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding-bottom: 40px;
  }

  .ab-list-header {
    display: flex; justify-content: space-between; align-items: center;
    margin-bottom: 16px;
    font-size: 13px; color: #64748b;
  }
  
  /* User Grid */
  .ab-user-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 16px;
  }
  
  .ab-user-card {
    background: white;
    border: 1px solid var(--tf-border);
    border-radius: 16px;
    padding: 20px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex; flex-direction: column; gap: 12px;
    position: relative;
  }
  .ab-user-card:hover { transform: translateY(-4px); box-shadow: var(--tf-shadow); border-color: var(--tf-primary); }
  .ab-user-card.selected { border: 2px solid var(--tf-primary); background: var(--tf-primary-soft); }
  
  .ab-card-top { display: flex; justify-content: space-between; align-items: flex-start; }
  .ab-avatar-wrapper { position: relative; }
  .ab-avatar {
    width: 48px; height: 48px;
    border-radius: 16px;
    background: #f1f5f9;
    display: flex; align-items: center; justify-content: center;
    font-weight: 700; font-size: 18px; color: #64748b;
  }
  .ab-status-badge {
    position: absolute; bottom: -2px; right: -2px;
    border: 2px solid white; border-radius: 50%;
  }
  
  .ab-fav-btn {
    background: none; border: none; cursor: pointer; padding: 4px;
  }
  
  .ab-user-info { display: flex; flex-direction: column; gap: 2px; }
  .ab-name-row { display: flex; align-items: baseline; gap: 4px; }
  .ab-name { font-weight: 700; font-size: 16px; color: var(--tf-text); }
  .ab-realname { font-size: 12px; color: #94a3b8; }
  .ab-dept { font-size: 12px; color: #64748b; margin-top: 2px; }
  .ab-status-msg { font-size: 12px; color: #64748b; margin-top: 4px; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; opacity: 0.8; }
  
  .ab-user-tags { display: flex; gap: 4px; flex-wrap: wrap; margin-top: auto; }
  
  .ab-user-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 16px;
    position: relative; /* Empty state positioning */
    min-height: 200px;
  }
  
  /* Empty State */
  .ab-empty-state {
    grid-column: 1 / -1; /*    */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px;
    color: #94a3b8;
    gap: 16px;
  }

  /* Status Dots */
  .ab-status-dot { width: 12px; height: 12px; border-radius: 50%; display: block; }
  .ab-status-dot.online { background: #22c55e; }
  .ab-status-dot.focus { background: #ef4444; box-shadow: 0 0 0 2px rgba(239, 68, 68, 0.2); }
  .ab-status-dot.busy { background: #f59e0b; }
  .ab-status-dot.offline { background: #cbd5e1; border: 2px solid #fff; }
  
  /* Drawer */
  .ab-drawer {
    width: 340px;
    background: white;
    border-left: 1px solid var(--tf-border);
    display: flex; flex-direction: column;
    transition: margin-right 0.3s ease;
    margin-right: -360px;
    flex-shrink: 0;
    border-radius: 16px 0 0 16px;
    box-shadow: -4px 0 12px rgba(0,0,0,0.05);
  }
  .ab-drawer.open { margin-right: 0; }
  
  .ab-drawer-header {
    padding: 20px;
    border-bottom: 1px solid var(--tf-border);
    display: flex; justify-content: space-between; align-items: center;
  }
  .ab-drawer-title { font-weight: 700; font-size: 16px; }
  .ab-close-btn { background: none; border: none; cursor: pointer; color: #94a3b8; }
  
  .ab-drawer-body { padding: 24px; overflow-y: auto; flex: 1; }
  
  .ab-profile-hero { display: flex; flex-direction: column; align-items: center; margin-bottom: 24px; }
  .ab-hero-avatar {
    width: 80px; height: 80px; border-radius: 24px;
    background: #f1f5f9;
    display: flex; align-items: center; justify-content: center;
    font-size: 32px; font-weight: 700; color: #64748b; margin-bottom: 16px;
  }
  .ab-hero-names { display: flex; flex-direction: column; align-items: center; gap: 2px; }
  .ab-hero-main-name { font-size: 20px; font-weight: 800; }
  .ab-hero-sub-name { font-size: 14px; color: #94a3b8; }
  .ab-hero-email { font-size: 13px; color: #94a3b8; margin: 12px 0; }
  .ab-hero-status-pill {
    display: flex; align-items: center; gap: 6px;
    padding: 6px 12px; border-radius: 99px; font-size: 12px;
    background: #f8fafc; color: var(--tf-text-muted);
  }
  .ab-hero-status-pill.focus { background: #fef2f2; color: #ef4444; border: 1px solid #fecaca; }
  
  .ab-quick-actions { display: flex; justify-content: space-around; margin-bottom: 24px; }
  .ab-action-btn {
    background: none; border: none; cursor: pointer;
    display: flex; flex-direction: column; align-items: center; gap: 8px;
    font-size: 12px; color: #64748b;
  }
  .ab-action-btn:hover { color: var(--tf-primary); }
  .ab-action-btn div { font-weight: 600; }
  
  .ab-info-section { margin-bottom: 24px; }
  .ab-label { font-size: 12px; font-weight: 700; color: #94a3b8; margin-bottom: 8px; display: block; }
  .ab-field { margin-bottom: 16px; }
  .ab-row { display: flex; align-items: center; gap: 10px; font-size: 14px; margin-bottom: 8px; }
  
  .ab-drawer-footer { margin-top: auto; padding-top: 20px; }
  
  /* Modals */
  .ab-modal-overlay {
    position: fixed; top: 0; left: 0; width: 100vw; height: 100vh;
    background: rgba(0,0,0,0.4); z-index: 2000;
    display: flex; align-items: center; justify-content: center;
    backdrop-filter: blur(2px);
  }
  .ab-modal {
    width: 360px; background: white; border-radius: 16px;
    box-shadow: 0 20px 50px rgba(0,0,0,0.2); overflow: hidden;
  }
  .ab-modal-header {
    padding: 16px 20px; border-bottom: 1px solid var(--tf-border);
    display: flex; justify-content: space-between; align-items: center;
  }
  .ab-modal-header h3 { margin: 0; font-size: 16px; font-weight: 700; }
  .ab-modal-header button { background: none; border: none; cursor: pointer; color: #94a3b8; }
  
  .ab-modal-body { padding: 20px; }
  .ab-input-row { display: flex; gap: 8px; }
  .ab-help-text { font-size: 12px; color: #94a3b8; margin-top: 8px; }
  
  .ab-group-manage-list { margin-top: 16px; max-height: 200px; overflow-y: auto; }
  .ab-group-manage-item {
    display: flex; justify-content: space-between; align-items: center;
    padding: 8px 12px; background: #f8fafc; border-radius: 8px; margin-bottom: 8px;
    font-size: 14px;
  }
  .ab-del-btn { background: none; border: none; cursor: pointer; color: #ef4444; opacity: 0.6; }
  .ab-del-btn:hover { opacity: 1; }
  
  .ab-modal-footer { margin-top: 24px; }
  .full-width { width: 100%; }

  /* Chips Style Override */
  .tf-chip.unassigned {
    background: #f1f5f9;
    color: #94a3b8;
    border: 1px dashed #cbd5e1;
  }

  /* Invite View Styles (New) */
  .ab-desc-text {
    font-size: 13px; color: #64748b; margin-bottom: 12px;
  }

  .ab-invite-view {
    display: flex; flex-direction: column; align-items: center; text-align: center;
    padding: 10px 0;
  }
  .ab-invite-icon {
    width: 64px; height: 64px; background: #fffbeb; border-radius: 50%;
    display: flex; align-items: center; justify-content: center;
