/* Group Manage Modal List */
.ab-group-manage-list {
  margin-top: 10px;
  flex: 1;
  overflow-y: auto;
  border-top: 1px solid var(--tf-border);
  padding-top: 10px;
}

.ab-group-manage-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: white;
  border: 1px solid var(--tf-border);
  border-radius: 8px;
  margin-bottom: 8px;
  font-size: 14px;
  transition: all 0.2s;
}
.ab-group-manage-item:hover {
  background-color: #f8fafc;
  border-color: #cbd5e1;
}

.ab-group-name-text {
  font-weight: 500;
  color: var(--tf-text);
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Edit Mode Styles */
.ab-group-edit-mode {
  display: flex;
  align-items: center;
  gap: 6px;
  width: 100%;
}
.tf-input.small {
  padding: 6px 10px;
  font-size: 13px;
  height: 32px;
}

/* Action Buttons */
.ab-group-actions {
  display: flex;
  align-items: center;
  gap: 4px;
}

.ab-icon-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 6px;
  border-radius: 4px;
  color: #94a3b8;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}
.ab-icon-btn:hover { background-color: #e2e8f0; color: #475569; }
.ab-icon-btn:disabled { opacity: 0.3; cursor: not-allowed; }

/* Specific Colors */
.ab-icon-btn.edit:hover { color: var(--tf-primary); background-color: var(--tf-primary-soft); }
.ab-icon-btn.del:hover { color: #ef4444; background-color: #fef2f2; }
.ab-icon-btn.save { color: #22c55e; }
.ab-icon-btn.save:hover { background-color: #ecfdf5; }
.ab-icon-btn.cancel:hover { background-color: #f1f5f9; color: #64748b; }

/* Modal Flex Layout Fix */
.ab-modal-body.flex-col {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}