/* src/main/frontend/src/styles/appShell.css */

.app-shell {
  height: 100vh;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

/* Header  (: LNB / : ) flex  */
.app-shell__body {
  flex: 1;
  min-height: 0; /*     overflow/100%       */
  display: flex;
}

/*  */
.app-shell__main {
  flex: 1;
  min-width: 0;
  min-height: 0;
  overflow: auto; /*    */
  padding: 24px;
  background: var(--color-bg, #f6f7fb);
}

/*   height:100%    */
.app-shell__main > * {
  min-width: 0;
}

/*      */
@media (max-width: 960px) {
  .app-shell__main {
    padding: 16px;
  }
}
